body {
  background-color: #eaf2f2;
  padding: 0;
  margin: 0;
  overflow: hidden; }

img {
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none; }

.loadingScreen {
  width: 100%;
  height: 100vh;
  background-color: #eaf2f2;
  display: flex;
  justify-content: center;
  align-items: center; }
  .loadingScreen .loadingContainer {
    width: 100px;
    height: 100px; }
    .loadingScreen .loadingContainer img {
      animation: rotateLoading 3s infinite ease-out; }

@keyframes rotateLoading {
  from {
    transform: rotate(0); }
  to {
    transform: rotate(360deg); } }

.socialBarContainer {
  float: left;
  width: 20%;
  height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column; }
  .socialBarContainer a {
    width: 50%;
    height: 10vh; }
    .socialBarContainer a .socialLink {
      background-repeat: no-repeat;
      width: 50%;
      height: 60%; }

.container {
  width: 100%;
  height: 100vh; }
  .container img {
    transform: scale(2); }
    .container img:hover {
      cursor: pointer;
      filter: invert(0.7); }

.middleContainer {
  float: left;
  width: 60%;
  height: 100vh;
  display: flex;
  align-items: center; }
  .middleContainer .middleLogos {
    position: relative;
    width: 40%;
    min-width: 90px;
    height: 100vh; }
    .middleContainer .middleLogos svg:nth-child(1) {
      position: absolute;
      width: 100px;
      left: 50%;
      top: 0;
      cursor: pointer; }
    .middleContainer .middleLogos svg:nth-child(2) {
      position: absolute;
      top: 25%; }

.menuContainer {
  width: 20%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  float: left; }

.menuLinkContainer {
  padding: 10px;
  margin: 0;
  width: 25%;
  height: 5vh; }

#goz_1,
#goz_2 {
  display: block; }

.handLeftContainer,
.handRightContainer {
  float: left;
  width: 30%;
  height: 70vh;
  position: relative; }
  .handLeftContainer img,
  .handRightContainer img {
    position: absolute;
    width: 100%;
    transform: rotateY(180deg); }
    .handLeftContainer img:hover,
    .handRightContainer img:hover {
      cursor: auto;
      filter: none; }

.handRightContainer img {
  transform: none; }

#lampColor {
  transition: 0.5s; }
